import React from 'react'
import { provideHooks } from 'redial'
import { connect } from 'react-redux'
import { compose } from 'redux'

// import CodeBlock from '../../components/CodeBlock'
import Heading from 'constructicon/heading'
import Helmet from 'react-helmet'
import RichText from 'constructicon/rich-text'

const hooks = {
  fetch: ({
    dispatch,
    state
  }) => Promise.all([])
}

const mapState = () => ({})

const State = () => {
  return (
    <div>
      <Helmet title='State | Services Engineering' />
      <Heading size={5}>State</Heading>
      <RichText>
        <p>We use <a href='https://github.com/reactjs/redux'>Redux</a> for application state management, when our application state starts to spread and becomes harder to manage. Redux stores the state (data) of our application in a single immutable state tree. It is important to understand the concepts of how we access and manipulate this state, and to understand that Redux is a simple concept at it's core.</p>
        <h3>State</h3>
        <h3>Reducer</h3>
        <h3>Actions / Dispatch</h3>
        <h3>Middlewares</h3>
      </RichText>
    </div>
  )
}

export default compose(
  connect(mapState),
  provideHooks(hooks)
)(State)



